@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;margin:15px 20px;font-family:'Roboto Condensed', sans-serif;min-height:100vh;display:flex;flex-direction:column}.App .content{flex:1 1}.App h1{font-style:normal;font-weight:normal;font-size:36px;line-height:42px;text-transform:uppercase}

.Gallery{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}

.GalleryImage{flex-basis:500px;margin-bottom:30px;padding:20px}.GalleryImage img{width:100%}.GalleryImage img:hover{cursor:pointer}

.Footer{border-top:1px solid #CCCCCC;padding:30px;color:#CCCCCC}

.Modal .flex-container{display:flex;justify-content:center;align-items:center;position:absolute;top:0;left:0;width:100%;height:100%}.Modal .modal-content{position:relative;background:white;border-radius:2px;z-index:10;box-shadow:2px 2px 10px rgba(0,0,0,0.3)}.Modal .modal-content.mobile{width:100%;min-height:100%}.Modal .close-modal{position:absolute;top:10px;right:10px}.Modal .close-modal:hover{cursor:pointer}.blackout{position:fixed;width:100%;height:100%;top:0;left:0;background:black;background-size:cover;opacity:0.5}

.BigImage{margin-bottom:40px}.BigImage img{width:100%}

.CommentForm .form-element{display:flex;flex-direction:column}.CommentForm .form-element input{margin-bottom:20px;border:1px solid #CCCCCC;border-radius:3px;height:30px}.CommentForm .form-element input[type="text"]{padding:0 10px}.CommentForm .form-element input[type="submit"]{background:#4997D0;color:white}.CommentForm .form-element input[type="submit"]:hover{cursor:pointer}

.Comments{min-width:200px;margin:0 20px 40px}.Comments .comment{margin-bottom:10px}.Comments .comment .comment-date{font-style:normal;font-weight:normal;font-size:13px;line-height:15px;color:#999999}.Comments .comment .comment-text{font-style:normal;font-weight:normal;font-size:13px;line-height:15px;display:flex}

